<template>
    <home-container title="KYC Information" default-href="/home/account/settings" :with-header='true'>
        <ion-label position='stacked'>Upload mode of identification</ion-label>
        <div class="d-flex flex-column justify-content-center align-items-center  text-center card card-body mb-4">
            <ion-icon :icon="cameraSharp" class='display-1 text-muted'></ion-icon>
            <ion-text><small >Upload your NIN, Int'l Passport, National ID or<br> Driver licence (JPEG OR PNG)</small></ion-text>
        </div>
        <ion-label position='stacked'>BVN</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-input placeholder="Enter your BVN here"></ion-input>
        </ion-item>
        
        <ion-button strong @click.prevent="openToast"  class='ion-margin-horizontal my-5' expand="block">Update KYC</ion-button>
    </home-container>
    
</template>
<script>
import { defineComponent } from "vue";
import { toastController } from "@ionic/vue";
import {cameraSharp} from "ionicons/icons";
import { useRouter } from "vue-router";

    export default defineComponent({
        components: { },
        data() {
            return {
                
            }
        },
        setup() {
            const router = useRouter();
            return {
                router,cameraSharp
            };
        },
        methods: {
            async openToast() {
                const toast = await toastController
                    .create({
                    message: 'Operation Successfull!',
                    duration: 2000,
                    color:'dark'
                    })
                return toast.present();
            }
        }
});

</script>
